.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.wrap{
  word-break: break-all;
  margin-right: 5px; 
  color: #0044BB;
  background-color: #CCEEFF;
  padding: 3px;
  border-radius: 6px;
}

.App-message {
  max-width: 500px;
  height: 300px;
  background: 	#FCFCFC;
  border-radius: 10px;
  margin: 20px;
  padding: 20px;
  overflow: auto;
}